//获取二维数组的key 和 value数组
export function getArrFromObj(arr) {
    let keys = [];
    let values = [];
    if(arr.length>0){
        arr.forEach(item =>{
            keys.push(item[0]);
            values.push(item[1]);
        })
    }
    return [keys,values];
}
//获取二维数组的key 和 value数组
export function getIndexOfArr(arr,value) {
    if(value!==null){
        value = String(value);
    }
    let index = arr.indexOf(value);
    return index;
}
